    font-family: 'Kosugi Maru';
    src: url('https://fonts.googleapis.com/css2?family=Kosugi+Maru&display=swap');
}

:root {
    --base-font-size: 18px;
    --base-spacing: 25px;
}

html {
    font-size: var(--base-font-size);
    -webkit-text-size-adjust: 100%;
}

body {
    margin: 0;
    padding: 0;
    min-height: 100vh;
    width: 100%;
    overflow-x: hidden;
}

.app {
    max-width: 800px;
    margin: 0 auto;
    padding: var(--base-spacing);
    font-family: 'Kosugi Maru', 'Helvetica Neue', Arial, sans-serif;
    width: 100%;
    box-sizing: border-box;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    position: relative;
    overflow-x: hidden;
}

h1 {
    text-align: center;
    color: #333;
    margin-bottom: 40px;
    font-size: clamp(36px, 8vw, 48px);
    line-height: 1.2;
}

.menu {
    text-align: center;
    margin-bottom: 50px;
    padding: 0 20px;
    width: 100%;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
}

.difficulty-button {
    display: block;
    width: min(400px, 95%);
    margin: 20px auto;
    padding: 25px;
    font-size: clamp(24px, 5vw, 28px);
    border: none;
    border-radius: 12px;
    background-color: #4CAF50;
    color: white;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.2s;
    -webkit-tap-highlight-color: transparent;
}

.difficulty-button:active {
    transform: scale(0.98);
}

.game {
    background-color: #f9f9f9;
    padding: clamp(20px, 5vw, 30px);
    border-radius: 12px;
    margin-bottom: 30px;
    width: 100%;
    box-sizing: border-box;
}

.game-info {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-bottom: 30px;
    font-size: clamp(22px, 5vw, 24px);
    padding: 0 15px;
}

.problem {
    text-align: center;
    margin: 30px 0;
    padding: 0 15px;
}

.problem h2 {
    font-size: clamp(36px, 8vw, 48px);
    margin-bottom: 30px;
    line-height: 1.3;
}

.problem form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    max-width: 600px;
    margin: 0 auto;
    width: 100%;
    position: relative;
    z-index: 1;
}

.problem input {
    padding: 20px;
    font-size: clamp(24px, 5vw, 28px);
    width: min(300px, 95%);
    border: 3px solid #ddd;
    border-radius: 12px;
    text-align: center;
    -webkit-appearance: none;
    appearance: none;
    background-color: white;
    position: relative;
    z-index: 1;
}

.problem button {
    padding: 20px 40px;
    font-size: clamp(24px, 5vw, 28px);
    background-color: #2196F3;
    color: white;
    border: none;
    border-radius: 12px;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.2s;
    width: min(300px, 95%);
    -webkit-tap-highlight-color: transparent;
}

.problem button:active {
    transform: scale(0.98);
}

.game-over {
    background-color: #f5f5f5;
    padding: clamp(30px, 7vw, 40px);
    border-radius: 15px;
    margin: 30px auto;
    max-width: min(550px, 95%);
    text-align: center;
}

.game-over h2 {
    color: #333;
    margin-bottom: 30px;
    font-size: clamp(36px, 7vw, 40px);
    line-height: 1.3;
}

.final-score {
    font-size: clamp(28px, 6vw, 32px);
    margin-bottom: 25px;
    color: #333;
    font-weight: bold;
}

.evaluation {
    font-size: clamp(40px, 8vw, 48px);
    margin-bottom: 40px;
    font-weight: bold;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
    padding: 20px 30px;
    border-radius: 20px;
    display: inline-block;
    animation: popIn 0.5s ease-out;
}

.evaluation.encourage {
    color: #2196F3;
    background-color: rgba(33, 150, 243, 0.1);
}

.evaluation.good {
    color: #4CAF50;
    background-color: rgba(76, 175, 80, 0.1);
}

.evaluation.excellent {
    color: #f44336;
    background-color: rgba(244, 67, 54, 0.1);
}

@keyframes popIn {
    0% {
        transform: scale(0.8);
        opacity: 0;
    }
    50% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
}

.game-over p {
    font-size: 20px;
    margin-bottom: 30px;
    color: #666;
}

.rankings {
    background-color: #f5f5f5;
    padding: 20px;
    border-radius: 10px;
}

.rankings h2 {
    text-align: center;
    margin-bottom: 20px;
}

.ranking-section {
    margin-bottom: 20px;
}

.ranking-section h3 {
    color: #333;
    margin-bottom: 10px;
}

.ranking-section ol {
    list-style-position: inside;
    padding: 0;
}

.ranking-section li {
    padding: 5px 0;
    border-bottom: 1px solid #ddd;
}

@keyframes correctAnswer {
    0% {
        transform: translate(-50%, -50%) scale(1);
        opacity: 1;
    }
    50% {
        transform: translate(-50%, -50%) scale(1.1);
        opacity: 1;
    }
    100% {
        transform: translate(-50%, -50%) scale(1);
        opacity: 0;
    }
}

@keyframes wrongAnswer {
    0% {
        transform: translate(-50%, -50%) scale(1);
        opacity: 1;
    }
    50% {
        transform: translate(-50%, -50%) scale(1.1);
        opacity: 1;
    }
    100% {
        transform: translate(-50%, -50%) scale(1);
        opacity: 0;
    }
}

.answer-feedback {
    position: fixed;
    top: 20%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: clamp(15px, 4vw, 20px) clamp(30px, 6vw, 40px);
    border-radius: 10px;
    color: white;
    font-size: clamp(24px, 6vw, 32px);
    font-weight: bold;
    z-index: 1000;
    pointer-events: none;
    text-align: center;
    width: min(300px, 80%);
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.answer-feedback.correct {
    animation: correctAnswer 1s ease-in-out forwards;
    background-color: #4CAF50;
}

.answer-feedback.wrong {
    animation: wrongAnswer 1s ease-in-out forwards;
    background-color: #f44336;
}

/*  */
@media (hover: none) {
    .difficulty-button:active,
    .problem button:active {
        opacity: 0.8;
    }
}

/*  */
@media (prefers-color-scheme: dark) {
    .app {
        background-color: #1a1a1a;
        color: #ffffff;
    }

    h1 {
        color: #ffffff;
    }

    .game {
        background-color: #2d2d2d;
    }

    .game-over {
        background-color: #2d2d2d;
    }

    .game-over h2 {
        color: #ffffff;
    }

    .final-score {
        color: #ffffff;
    }

    .problem input {
        background-color: #3d3d3d;
        color: #ffffff;
        border-color: #4d4d4d;
    }
}

@media (min-width: 768px) {
    .game-info {
        flex-direction: row;
        justify-content: space-between;
    }

    .problem form {
        flex-direction: row;
        justify-content: center;
    }

    .problem button {
        width: auto;
    }
}

@media (max-width: 480px) {
    :root {
        --base-font-size: 16px;
        --base-spacing: 20px;
    }

    .app {
        padding: 20px;
        overflow-x: hidden;
    }

    .difficulty-button,
    .problem input,
    .problem button {
        width: 100%;
        padding: 25px;
        max-width: 100%;
    }

    .answer-feedback {
        top: 15%;
        font-size: clamp(28px, 7vw, 36px);
    }
